.business {
  background-color: var(--back-main);
}

.business__list.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, max-content));
  column-gap: 95px;
  row-gap: 60px;
  padding-top: 45px;
  padding-left: 200px;
  padding-right: 200px;
  padding-bottom: 57px;
}

.business__list p,
.business__list a {
  font-size: 25px;
  color: var(--basic-white);
  margin: 0;
}

.business__list p {
  margin-bottom: 35px;
}

p.business__copyright {
  font-size: 34px;
  font-variation-settings: "wght" 400, "wdth" 100;
  margin-bottom: 50px;
}

.business__item:nth-child(3) {
  padding-left: 30%;
}

p.business__title,
p.business__tel {
  font-size: 36px;
}

p.business__title {
  margin-bottom: 50px;
}

a.business__tel {
  display: block;
  line-height: 42px;
  text-decoration: none;
}

.business__info a {
  font-weight: 100;
}
