.info-card {
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 28px;
}

.info-card p {
  font-family: inherit;
  font-size: 25px;
  line-height: 24px;
  font-variation-settings: "wght" 400, "wdth" 90;
  color: var(--back-main);
  text-align: center;
  margin: 0;
  margin-top: 24px;
}

.info-card span {
  font-size: 42px;
  font-variation-settings: "wght" 400, "wdth" 90;
  color: #ef1993;
}

span.info-card__old-price {
  font-size: 25px;
  color: var(--back-dark-gray);
  text-decoration: line-through;
}

.info-card__link {
  display: block;
  margin-top: 28px;
}

.info-card__link.site-link {
  font-size: 25px;
  line-height: 28px;
  font-variation-settings: "wght" 400, "wdth" 90;
}

.info-card__link.site-link::after {
  position: absolute;
  content: "";
  width: 45%;
  height: 4px;
  left: 50%;
  transform: translateX(-50%);
  bottom: -4px;
  text-decoration: none;
  z-index: 3;
}
