.site-link {
  position: relative;
  color: var(--back-main);
  text-decoration: none;
}

.site-link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 4px;
  left: 0;
  bottom: -4px;
  text-decoration: none;
  z-index: -1;
}

.site-link--pink::after {
  background-color: var(--basic-dark-pink);
}

.site-link--orange::after {
  background-color: var(--basic-orange);
}

.site-link--green::after {
  background-color: var(--basic-green);
}

.site-link--cool-pink::after {
  background-color: var(--back-link);
}

.site-link--blue::after {
  background-color: var(--basic-blue);
}
