.field-details {

}

.field-details__list {
	display: grid;
	grid-template-columns: repeat(4, max-content);
	column-gap: 55px;
  row-gap: 25px;
  justify-content: space-between;
  margin-top: 25px;
}

.field-details__item label {
  position: relative;
  font-size: 25px;
  padding-left: 50px;
}

.field-details__item input[type="checkbox"] + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background: transparent;
  border: 1px solid var(--back-input);
}

.field-details__item input[type="checkbox"]:checked + label::after {
  position: absolute;
  content: "";
  top: 0;
  left: 5px;
  width: 25px;
  height: 25px;
  background: url("../img/color-circle.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: 3;
}
