.catalog {
  padding-top: 38px;
}

.catalog a {
  text-decoration: none;
}

.catalog__present {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, max-content));
  align-items: center;
  column-gap: 264px;
  padding-left: 132px;
}

.catalog__present p {
  display: block;
  padding-right: 120px;
  margin-top: 32px;
}

.catalog__cup {
  width: 235px;
  height: 235px;
}

.catalog__list {
  margin-top: 134px;
}
