.form-user.container {
  padding-top: 42px;
  padding-left: 200px;
  padding-right: 200px;
  padding-bottom: 64px;
}

.form-user__present {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  align-items: center;
  margin-bottom: 85px;
}

.form-user__present p {
  padding-left: 140px;
}

.form-user__filter {
	max-width: 1336px;
  margin-top: 90px;
}

.form-user__filter {
  font-size: 34px;
  line-height: 38px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  margin: 0;
}

.form-user__field {
	padding: 0;
	margin: 0;
	margin-bottom: 58px;
	border: none;
}

.form-user__filter input[type="checkbox"] + label,
.form-user__filter input[type="radio"] + label {
  cursor: pointer;
}

.form-user__filter input,
.form-user__filter textarea {
  font-size: 20px;
  color: var(--back-input);
}

.form-user__note {
  font-size: 25px;
  line-height: 28px;
  margin: 0;
  margin-bottom: 10px;
}

.form-user__note a {
  color: var(--back-main);
}

input.form-user__btn {
  display: block;
  font-size: 34px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  margin-left: auto;
}
