.field-profiles {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	column-gap: 184px;
}

.field-profiles__item {
	margin: 0;
}

.field-profiles__item label {
	display: block;
}

.field-profiles__item input[type="text"],
.field-profiles__item input[type="tel"] {
	width: 100%;
	height: 34px;
  padding-left: 10px;
	border: none;
	border-bottom: 2px solid var(--back-input);
}
