.gallery-card {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 346px));
  grid-auto-rows: minmax(584px, auto);
  column-gap: 125px;
  row-gap: 50px;

  @media (max-width: 1600px) {
    column-gap: 110px;
  }
}

.gallery-card article {
  width: 346px;
  min-height: 584px;
}

.gallery-card picture {
  width: 346px;
  height: 420px;
}

.gallery-card__item {
  position: relative;
}

.gallery-card__item::before {
  position: absolute;
  content: "";
  width: 122px;
  height: 122px;
  top: -40px;
  left: 270px;
}

.gallery-card__item--new::before {
  background-color: var(--basic-green);
}

.gallery-card__item--percent::before {
  background-color: var(--basic-yellow);
}

.gallery-card__item--hit::before {
  background-color: var(--back-link);
}

.gallery-card__item::after {
  position: absolute;
  width: 100px;
  right: -33px;
  font-family: inherit;
  font-variation-settings: "wght" 400, "wdth" 100;
  text-align: center;
  color: var(--basic-white);
  z-index: 5;
}

.gallery-card__item--new::after {
  content: "новинка";
  font-size: 25px;
  line-height: 25px;
  top: 7px;
}

.gallery-card__item--percent::after {
  content: "%";
  font-size: 75px;
  line-height: 55px;
  top: 0;
}

.gallery-card__item--hit::after {
  content: "хит продаж";
  font-size: 25px;
  line-height: 25px;
  top: 0;
}
