.promotion {
  position: relative;
}

.promotion > p {
  position: absolute;
  width: 375px;
  top: -5%;
  left: 50%;
  transform: translateX(-50%);
  font-family: inherit;
  font-size: 40px;
  line-height: 45px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  text-align: center;
}
