.users-gallery {
  padding-top: 115px;
}

.users-gallery__present {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 175px;
  align-items: center;
  padding-left: 132px;
  padding-bottom: 47px;
}

.users-gallery h3,
.users-gallery p {
  margin: 0;
}

.users-gallery p {
  padding-right: 180px;
}
