.site-list {
  display: grid;
  grid-template-columns: repeat(5, max-content);
  column-gap: 40px;
  row-gap: 40px;
  margin-left: 40px;
}

.site-list a {
  position: relative;
  font-size: 20px;
  line-height: 28px;
  color: var(--basic-white);
  text-decoration: none;
}

.site-list__item--active a {
  color: var(--back-link);
}
